import React from "react";
import { Switch, Route } from "react-router-dom";
import Home from "./components/Home";
import Instagram from "./components/Instagram";
import Linkedin from "./components/Linkedin";
import Twitter from "./components/Twitter";
import Pinterest from "./components/Pinterest";
import Facebook from "./components/Facebook";
import { Container } from "react-bootstrap";
import "./css/style.css";

import runtimeEnv from "@mars/heroku-js-runtime-env";
const env = runtimeEnv();
function App() {
  return (
    <Container
      fluid="sm"
      className="vh-100 d-flex justify-content-center flex-column"
    >
      <Switch>
        <Route path="/" component={Home} exact />
        {env.REACT_APP_TWITTER === "true" ? (
          <Route path="/Twitter" component={Twitter} exact />
        ) : (
          ""
        )}
        {env.REACT_APP_LINKEDIN === "true" ? (
          <Route path="/LinkedIn" component={Linkedin} exact />
        ) : (
          ""
        )}
        {env.REACT_APP_INSTAGRAM === "true" ? (
          <Route path="/Instagram" component={Instagram} exact />
        ) : (
          ""
        )}
        {env.REACT_APP_PINTEREST === "true" ? (
          <Route path="/Pinterest" component={Pinterest} exact />
        ) : (
          ""
        )}
        {env.REACT_APP_FACEBOOK === "true" ? (
          <Route path="/Facebook" component={Facebook} exact />
        ) : (
          ""
        )}
      </Switch>
    </Container>
  );
}

export default App;
