import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import runtimeEnv from "@mars/heroku-js-runtime-env";
import { Container, Row, Col, Card } from "react-bootstrap";

import Header from "./Header";
import Footer from "./Footer";
import instagramLogo from "../images/instagram-logo.svg";
import linkedinLogo from "../images/linkedin-logo.svg";
import facebookLogo from "../images/facebook-logo.svg";
import twitterLogo from "../images/twitter-logo.svg";
import pinterestLogo from "../images/pinterest-logo.svg";

const env = runtimeEnv();
console.log(env.REACT_APP_URL);
console.log(window.location.host);
export default class Home extends Component {
  state = {
    redirect: false,
    to: "",
  };
  setRedirect = (url) => {
    this.setState({
      redirect: true,
      to: url,
    });
  };
  renderRedirect = () => {
    if (this.state.redirect) {
      return <Redirect to={this.state.to} />;
    }
  };
  componentDidMount() {
    const favicon = document.getElementById("FavIcon");
    if (
      env.REACT_APP_FAVICONURL !== undefined ||
      env.REACT_APP_FAVICONURL !== ""
    )
      favicon.href = `${env.REACT_APP_FAVICONURL}`;
    const title = document.getElementById("AppTitle");
    if (env.REACT_APP_TITLE !== undefined || env.REACT_APP_TITLE !== "")
      title.innerText = `${env.REACT_APP_TITLE}`;
    else {
      title.innerText = "Media Login Panel";
    }
  }
  render() {
    return (
      <>
        <Header />
        <div className="col2 col-center">
          <Container>
            <Card className="h-95">
              <Card.Header>
                <h4 className="card-header-title mb-0">
                  What Website Would You Like to Login To?
                </h4>
              </Card.Header>
              <Card.Body className="text-center">
                {this.renderRedirect()}
                {env.REACT_APP_INSTAGRAM === "true" ? (
                  <div>
                    <a
                      class="btn btn-lg btn-primary lift btn-list btn-selection"
                      onClick={() => this.setRedirect("/Instagram")}
                    >
                      <img src={instagramLogo} class="img-selection" />
                    </a>
                  </div>
                ) : (
                  ""
                )}

                {env.REACT_APP_FACEBOOK === "true" ? (
                  <div>
                    <a
                      class="btn btn-lg btn-primary lift btn-list btn-selection"
                      onClick={() => this.setRedirect("/Facebook")}
                    >
                      <img src={facebookLogo} class="img-selection" />
                    </a>
                  </div>
                ) : (
                  ""
                )}

                {env.REACT_APP_PINTEREST === "true" ? (
                  <div>
                    <a
                      class="btn btn-lg btn-primary lift btn-list btn-selection"
                      onClick={() => this.setRedirect("/Pinterest")}
                    >
                      <img src={pinterestLogo} class="img-selection" />
                    </a>
                  </div>
                ) : (
                  ""
                )}

                {env.REACT_APP_TWITTER === "true" ? (
                  <div>
                    <a
                      class="btn btn-lg btn-primary lift btn-list btn-selection"
                      onClick={() => this.setRedirect("/Twitter")}
                    >
                      <img src={twitterLogo} class="img-selection" />
                    </a>
                  </div>
                ) : (
                  ""
                )}

                {env.REACT_APP_LINKEDIN === "true" ? (
                  <div>
                    <a
                      class="btn btn-lg btn-primary lift btn-list btn-selection"
                      onClick={() => this.setRedirect("/Linkedin")}
                    >
                      <img src={linkedinLogo} class="img-selection" />
                    </a>
                  </div>
                ) : (
                  ""
                )}
              </Card.Body>
            </Card>
          </Container>
        </div>
        <Footer />
      </>
    );
  }
}
